html, body {
  overflow-x: hidden;
}

/* BASKET */
#basket{
  position: fixed;
  bottom: 40px;
  right: 60px;
  z-index: 200;
}

/* NAV */
#nav{
  background: linear-gradient(rgba(255,0,0,0), rgba(0,0,0,0)), url();
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  font-family: 'Righteous', cursive;
}

.navbar-brand{
  font-size: 2rem!important;
  font-family: 'Pacifico', cursive!important;
  color: white !important;
}

.nav-link{
  color: white!important;
}

.nav-link:hover{
  color: rgb(211, 211, 211)!important;
}

/* LANDING */
.landing{
  width: 100%;
  height: 100vh;
  background-image: url(./imgs/landing.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.landing-title{
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Pacifico', cursive;
}

.landing-title h1{
  font-size: 5rem;
}

/* FEATURED/SALES */
#featured{
  font-family: 'Righteous', cursive;
}

.hr-featured{
  border-top: 2px solid black;
  width: 80%;
}

.title-txt{
  font-size: 3.5rem;
}

.product-tile{
  color: black!important;
}

.subscribe{
  color: black;
  text-decoration: underline;
}

.subscribe:hover{
  color: rgb(235, 21, 146);
  text-decoration: underline;
}

.btn{
  background-color: #ce2da8!important;
  color: white!important;
}

.btn:hover{
  color: black!important;
}

.loading-opacity{
  opacity: .5;
}

.loading-gif{
  position: absolute;
}

  /* FEATURED SECTION */
  .tile-type-size{
    font-size: 1rem;
    color: rgb(235, 21, 146);
  }

/* DEPOP */
#depop{
  background: linear-gradient(rgba(0,0,0,.8), rgba(0,0,0,.8)), url(./imgs/depop-bg.jpg);
  font-family: 'Righteous', cursive;
}

#depop:hover{
  background: linear-gradient(rgba(0,0,0,.7), rgba(0,0,0,.7)), url(./imgs/depop-bg.jpg);
}

#depop a{
  text-decoration: none;
}

.depop{
  padding: 8rem;
  font-size: 8rem;
  color: white;
}

/* CATEGORIES */
.cat-tile{
  /*background: linear-gradient(rgba(0,0,0,.8), rgba(0,0,0,.8)), url(./imgs/depop-bg.jpg);*/
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 40vh;
}

.cat-tile h1{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  background-color: #db79c4;
  padding: 0 1.5rem;
  font-family: 'Pacifico', cursive!important;
}

.cat-tile:hover{
  background-color: #e915b7;
}

.cat-pad{
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.cat-img{
  height: 100%;
  width: 100%;
}

/* LOOK-BOOK */
#look-book{
  background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0)), url(./imgs/look-book.jpg);
  font-family: 'Righteous', cursive;
}

#look-book h2{
  font-size: 3rem;
}

.lb-txt{
  color: white;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  background-color: #e915b7;
}

.lb-tile:hover{
  background-color: #9283eb;
}

/* FOOTER */
.footer-title{
  font-size: 4rem!important;
  font-family: 'Pacifico', cursive!important;
}

.hr-footer{
  border-top: 1px solid black;
}

.footer-col{
  font-family: 'Righteous', cursive;
}
  
.footer-col a{
  color: black!important;
}

.footer-col a:hover{
  color: #e915b7!important;
}

#footer h3{
  font-size: 1rem;
  font-family: 'Righteous', cursive;
}

/* Extra small devices (portrait phones, less than 576px)
 No media query for `xs` since this is the default in Bootstrap */

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 577px) { 

/* LANDING */
.landing-title h1{
  font-size: 3rem;
}

.landing-title h3{
  font-size: 1rem;
}

/* FEATURED/SALES */
.title-txt{
  font-size: 2rem;
}

/* DEPOP */
.depop{
  font-size: 6rem;
}

}

/* Medium devices (tablets, 768px and up) */
@media (max-width: 769px) {
/* LANDING */

/* FEATURED/SALES */
.offers{
  display: none;
}

/* CATEGORIES */
.cat-pad{
  margin-top: 0;
  margin-bottom: 0;
}

}
/* Large devices (desktops, 992px and up) */
@media (max-width: 993px) {  
/* LANDING */

}

/* Extra large devices (large desktops, 1200px and up) */
@media (max-width: 1201px) {  
/* LANDING */

/* LOOK BOOK */
.lb-pad{
  padding-bottom: 2rem;
  padding-top: 2rem;
}

}